import { Injectable } from '@angular/core';
import { Observable,Subject } from 'rxjs';
import { Example } from '../../../domain/models/example/RootObject';


@Injectable()
export class CommunicationService {
    constructor() {
    }
	private sub_updateTransaction = new Subject<Example>();
	public obs_updateTransaction: Observable<Example> = this.sub_updateTransaction.asObservable();

	public updateTransaction(transaction: Example) {
		this.sub_updateTransaction.next(transaction);
	}
}